import React from 'react';
import {useParams} from "react-router-dom"
import OutpatientReportsByOrganizationsContainer from "../containers/OutpatientReportsByOrganizationsContainer";

const OutpatientReportsByOrganizationsPage = () => {
    const {year, month,state} = useParams()
    return (
        <>
            <OutpatientReportsByOrganizationsContainer year={year} month={month} state={state}/>
        </>
    );
};

export default OutpatientReportsByOrganizationsPage;